import React from 'react'
import { Link } from 'react-router-dom'
import logo from './logo.svg'
import './Header.css'
import SearchIcon from "@material-ui/icons/Search";
import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import { useStateValue } from "./StateProvider";
import Checkout from './Checkout';

function Header() {
    const [ {basket}] = useStateValue();
    return (
        <nav className="header">
            {/*Logo on the left*/}
            <Link to="/">
                <img src={logo} className="Header__logo" alt="logo" />
            </Link>
            {/*Seach box*/}
                <div className="Header__search">
                    <input type="text" className="Header__searchBar" />
                    <SearchIcon className="Header__searchIcon" />
                </div>

            <div className="Header__nav">
                {/*Link to About*/}
                {/*Link to Contact*/}
                {/* Link to Basket*/}
                <Link to="/About" className="Header__link">
                    <div className="header__option">
                        <span className="header__optionLineOne">Hello Dorian</span>
                        <span className="header__optionLineTwo">Sign in</span>
                    </div>
                </Link>

                <Link to="/Contact" className="Header__link">
                    <div className="header__option">
                        <span className="header__optionLineOne">Contact</span>
                        <span className="header__optionLineTwo">Message</span>
                    </div>
                </Link>

                <Link to="/Checkout" className="Header__link">
                    <div className="header__optionBasket">
                        {/* Shopping basket icon*/}
                        <ShoppingBasketIcon />
                        {/* Number of basket item*/}
                        <span className="header__optionCount">{basket.length}</span>
                    </div>
                </Link>
            </div>
        </nav>
    )
}

export default Header
